<template>
  <div class="head">
    <a @click="_back" class="left"><i class="back"></i>返回</a>
    <p>{{title}}</p>
    <div href="javascript:;" class="right" :class="{adClas: downBooler}">
      <span v-text="down" @click="downList()"></span>
      <a class="_backHome" href='iapp/home'><i></i></a>
    </div>
    <ul class="venue-down" v-if='listBooler'>
      <li class="venueIco">
        <router-link to="/venuePriority">场馆优先</router-link>
      </li>
      <li class="timeIco">
        <router-link to="/timePriority">时间优先</router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data () {
    return {
      listBooler: false,
      backbol: true
    }
  },
  props: {
    title: String,
    headList: Array,
    down: String,
    booler: Boolean,
  },
  methods: {
    _back() { 
      const paths = this.$route.params
      const pat = this.$route.path
      const userAgent = window.navigator.userAgent;
      if(userAgent.indexOf("ys_app")!=-1){
        if(pat !== "/venueIndex") {
          if(paths.paths){
            this.$router.push(paths.paths);
          }else{
            this.$router.go(-1);
          }
        }
      } else {
        if(paths.paths){
          this.$router.push(paths.paths);
        }else{
          this.$router.go(-1);
        }
      }
    },
    downList() {
      this.listBooler = true;
      setTimeout(() => {
        this.listBooler = false
      }, 2000);
    }
  },
  computed: {
    downBooler () {
      return this.down ==''? true: false
    }
  }
}
</script>
<style lang="less">
.head {
  height: 2.8125rem /* 45/16 */;
  background: #1f95ff;
  display: flex;
  justify-content: space-between;
  p {
    color: #fff;
    line-height: 2.8125rem; 
  }
  .left {
    display: inline-block;
    height: 100%;
    width: 84px;
    text-indent: 37px;
    line-height: 45px;
    position: relative;
    color: #fff;
    .back {
      content: "";
      display: block;
      position: absolute;
      border-left: 2px solid #fff;
      border-top: 2px solid #fff;
      transform: rotate(-45deg);
      width: 12px;
      height: 12px;
      left: 20px;
      top: 50%;
      margin-top: -7px;
    }
  }
  .right {
    width: 5.4375rem; /* 87/16 */
    line-height: 2.8125rem; /* 45/16 */
    color: #fff;
    i {
      display: inline-block;
      height: 1.25rem; /* 20/16 */
      width: 1.25rem; /* 20/16 */
      background: url(/static/images/index0.png) center center no-repeat;
      background-size: contain;
      vertical-align: middle;
      margin-bottom: 3px;
    }
    &.adClas {
      width: 2.5rem /* 40/16 */;
      text-align: right;
      padding-right: 1rem /* 16/16 */;
    }
  }
  .venue-down {
    position: absolute;
    right: 1.625rem;
    top: 2.875rem;
    z-index: 9999;
    background: #333;
    border-radius: .3125rem;
    &::before {
      content: '';
      display: block;
      width: 0;
      height: 0;
      border-top: 8px solid transparent;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-bottom: 8px solid #333;
      position: absolute;
      top: -16px;
      right: 23px;
    }
    li:not(:last-child) {
      border-bottom: 1px solid #464545;
    }
    li{
      position: relative;
      &::before {
        content: '';
        display: block;
        width: 18px;
        height: 18px;
        position: absolute;
        top: 50%;
        margin-top: -9px;
        margin-left: 9px;
      }
      &.venueIco::before {
        background: url(/static/images/venueIco.png) no-repeat;
        background-size: 16px;
      }
      &.timeIco::before {
        background: url(/static/images/timeIco.png) no-repeat;
        background-size: 16px;
      }
    }
    a {
      color: #fff;
      display: inline-block;
      padding: .4375rem 1.25rem;
      padding-left: 2rem /* 32/16 */;
    }
  }
}
</style>


